.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.alerta{
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: #1d2b3a;
  margin: 20px;
  color: red;
  width: 750px;
  padding: 10px;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-color: #334d49b2;
  width: 800px;
  /* padding: 150px; */
  box-sizing: border-box;
  /* margin:20px; */
  border-radius: 20px;
  font-family: "Gloria Hallelujah", cursive;
  font-size: 1rem;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  color: white;
}

.inputBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 600px;
  margin: 20px;
}

.inputBox > .input {
  width: 100%;
  padding: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: #1d2b3a;
  font-size: 15px;
  color: rgba(255, 255, 255, 0.62);
  border-radius: 10px;
}
.inputBox > .date {
  width: 100%;
  padding: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: #1d2b3a;
  color: rgba(255, 255, 255, 0.62);
  border-radius: 10px;
}

.inputBox > .date::-webkit-calendar-picker-indicator{
  padding: 5px;
  cursor: pointer;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.62);
  box-shadow: 3px 3px 6px rgba(255, 255, 255, 0.25),
                -3px -3px 6px rgba(255, 255, 255, 0.25);
}

.rangeBox {
  position: relative;
  width: 250px;
}
.rangeBox > .range {
  width: 100%;
  -webkit-appearance: none;
  background: #1d2b3a;
  border-radius: 10px;
  
}


.rangeBox > .range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.25);
}
.rangeBox > .number {
  border: 1px solid rgba(255, 255, 255, 0.25);
  background-color: #1d2b3a;
  border-radius: 20px;
  color: rgba(255, 255, 255, 0.62);
  padding: 0 10px;
}

.inputBox > .option {
  text-align: center;
  width: 100%;
  background: #1d2b3a;
  color: rgba(255, 255, 255, 0.62);
  font-size: 15px;
  border-radius: 10px;
}

.inputBox > .div {
  
  box-sizing: border-box;
  width: 50%;
  height: auto;
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: #1d2b3a;
  border-radius: 10px;
  margin: 5px;
}

.a {
  margin-top: 100px;
  position: relative;
  background: #0979aa;
  color: #fff;
  text-decoration: none;
  /* text-transform: uppercase; */
  font-size: 1.5em;
  letter-spacing: 0.1em;
  padding: 10px 30px;
  transition: 0.5s;
  bottom: -100px;
  left: -30rem;
  text-shadow: 0 0 10px #01843a, 0 0 20px #01843a, 0 0 40px #01843a,
    0 0 80px #01843a, 0 0 120px #01843a;
}

.a:hover {
  letter-spacing: 0.25em;
  background: var(#1e9bff);
  color: var(#1e9bff);
  box-shadow: 0 0 35px var(#1e9bff);
}

.a::before {
  content: "";
  position: absolute;
  inset: 2px;
  background: #27282c;
}

.a > span {
  position: relative;
  z-index: 1;
}

.a > i {
  position: absolute;
  inset: 0;
  display: block;
}

.a > i::before {
  content: "";
  position: absolute;
  top: -3.5px;
  left: 80%;
  width: 10px;
  height: 5px;
  border: 2px solid #1e9bff;
  background: #27282c;
  transform: translateX(-50%);
  transition: 0.5s;
}

.a:hover > i::before {
  width: 20px;
  left: 20%;
}

.a > i::after {
  content: "";
  position: absolute;
  bottom: -3.5px;
  left: 20%;
  width: 10px;
  height: 5px;
  border: 2px solid #1e9bff;
  background: #27282c;
  transform: translateX(-50%);
  transition: 0.5s;
}

.a:hover > i::after {
  width: 20px;
  left: 80%;
}

.div > .buttonSelect {
  background: black;
  text-shadow: 0 0 2.5px #01843a, 0 0 5px #01843a, 0 0 10px #01843a,
  0 0 80px #01843a, 0 0 120px #01843a;
    color:#fff;
    border-radius: 5px;
  border: 3px solid rgba(255, 255, 255, 0.25);
  margin: 10px;
  cursor: pointer;
  letter-spacing: 1.5px;
  padding: 3.5px;
}

.form >.boxButton{
  
  margin: 25px;
 
}

.form> .boxButton > .button{
  color: rgba(255, 255, 255, 0.62);
  background-color:#1d2b3a ;
  border-radius: 5px;
  padding: 10px;
}
